
import { defineComponent, reactive, ref } from "vue";
import { UserOutlined, LockOutlined } from "@ant-design/icons-vue";
import { useRouter } from "vue-router";

export default defineComponent({
  name: "login",
  components: {
    UserOutlined,
    LockOutlined
  },
  setup() {
    const router = useRouter();

    const userInfo = reactive({
      account: "",
      password: "",
      alertMessage: "",
      autoLogin: false,
      ForgotPassword: false
    });

    const rules = reactive({
      account: [{ required: true, message: "请输入账户名", trigger: blur }],
      password: [{ required: true, message: "请输入密码", trigger: blur }]
    });

    const loginCheck = ref<{ validate: any; resetFields: any } | null>(null);

    const closeAlert = () => {
      userInfo.alertMessage = "";
      userInfo.ForgotPassword = false;
    };

    const login = () => {
      const res = loginCheck.value?.validate();
      if (res) {
        router.push("/dashboard/big-data");
      }
    };

    const forgetPwd = () => {};

    return {
      title: "神木能源安监平台",
      englishTitle: "SHENMU DATA MONITORING",
      userInfo,
      rules,
      loginCheck,
      closeAlert,
      login,
      forgetPwd
    };
  }
});
